document.addEventListener("DOMContentLoaded", function () {
  const main = new Main();
});

class Main {
  constructor() {
    this.header = document.querySelector(".header");
    this.sides = document.querySelectorAll(".side");
    this._observers = [];
    this._init();
  }

  set observers(val) {
    this._observers.push(val);
  }

  get observers() {
    return this._observers;
  }

  _init() {
    new MobileMenu();
  }

  _paceDone() {
    this._scrollInit();
  }

  _inviewAnimation(el, inview) {
    if (inview) {
      el.classList.add("inview");
    } else {
      el.classList.remove("inview");
    }
  }

  _navAnimation(el, inview) {
    if (inview) {
      this.header.classList.remove("triggered");
    } else {
      this.header.classList.add("triggered");
    }
  }

  _sideAnimation(el, inview) {
    if (inview) {
      this.sides.forEach((side) => side.classList.add("inview"));
    } else {
      this.sides.forEach((side) => side.classList.remove("inview"));
    }
  }

  _textAnimation(el, inview) {
    if (inview) {
      const ta = new TweenTextAnimation(el);
      ta.animate();
    }
  }

  _toggleSlideAnimation(el, inview) {
    if (inview) {
      this.hero.start();
    } else {
      this.hero.stop();
    }
  }

  _destroyObservers() {
    this.observers.forEach((ob) => {
      ob.destroy();
    });
  }

  destroy() {
    this._destroyObservers();
  }

  _scrollInit() {
    this.observers = new ScrollObserver(
      ".nav-trigger",
      this._navAnimation.bind(this),
      { once: false }
    );
    this.observers = new ScrollObserver(".cover-slide", this._inviewAnimation);
    this.observers = new ScrollObserver(".appear", this._inviewAnimation);
    this.observers = new ScrollObserver(
      ".tween-animate-title",
      this._textAnimation,
      { rootMargin: "-200px 0px" }
    );
    this.observers = new ScrollObserver(
      ".swiper-container",
      this._toggleSlideAnimation.bind(this),
      { once: false }
    );
    this.observers = new ScrollObserver(
      "#main-content",
      this._sideAnimation.bind(this),
      { once: false, rootMargin: "-300px 0px" }
    );
  }
}

// ヘッダースクロール色変更
jQuery(window).on("scroll", function () {
  if (60 < jQuery(this).scrollTop()) {
    jQuery(".header").addClass("change-color");
  } else {
    jQuery(".header").removeClass("change-color");
  }
});

jQuery(window).on("scroll", function () {
  if (400 < jQuery(this).scrollTop()) {
    jQuery(".header").addClass("change-color");
  } else {
    jQuery(".header").removeClass("change-color");
  }
});

jQuery(window).on("scroll", function () {
  if (750 < jQuery(this).scrollTop()) {
    // 1000px以上スクロールしたら
    jQuery("#global-nav").addClass("m_fixed");
  } else {
    jQuery("#global-nav").removeClass("m_fixed");
  }
});

jQuery(function ($) {
  var sideBar = $(".footer__scroll");
  sideBar.hide();

  $(window).scroll(function () {
    if ($(this).scrollTop() > 200) {
      sideBar.fadeIn();
    } else {
      sideBar.fadeOut();
    }
  });
});

jQuery(function ($) {
  $(".js-accordion-title").on("click", function () {
    /*クリックでコンテンツを開閉*/
    $(this).next().slideToggle(200);
    /*矢印の向きを変更*/
    $(this).toggleClass("open", 200);
  });
});
